.header {
    position: fixed;
    width: 100%;
    height: 56px;
    box-sizing: border-box;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    background-color: white;
    height: 50px;
  }
  .container {
    margin-top: 56px;
    padding: 24px 36px 24px 24px;
    background-color: #f9f9f9;

  }
  .grid {
    display: grid;
    width: 100%;
    row-gap: 40px;
    column-gap: 16px;
    grid-template-columns: repeat(auto-fit, minmax(300px, auto));
  }
  .cate {
    position: fixed;
    width: 100%;
    height: 56px;
    box-sizing: border-box;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    background-color: white;
    height: 50px;
    list-style: none;
    display: flex;
    margin: 0px;
    padding: 0;
  }
  .cate li{
    width: 100px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border-radius: 32px;
    background-color: #E5E5E5;
    border: 1px solid #D9D9D9;
    cursor: pointer;
    margin-left: 15px;
    margin-top: 8px;
    font-size: 14px;
  }
  .setCate{
    color: white;
    border: 1px solid #D9D9D9;
    background-color: rgb(25, 25, 25) !important;
  }